e-in-out infinite;\n}\n#yawnRightHeader {\n  width: 160px;\n  animation: float 1.6s ease-in-out infinite, shake 1.6s linear infinite;\n}\n\n@keyframes float {\n  0% { \n    transform: translate(0, 0px); \n  } \n  25% { \n    transform: translate(0, 4px); \n  } \n  50% { \n    transform: translate(0, 0px); \n  } \n  75% { \n    transform: translate(0, -4px); \n  } \n  100% { \n    transform: translate(0, 0px); \n  } \n}\n\n@keyframes shakeOnly {\n  0% { \n    transform: rotate(1deg); \n  } \n  25% { \n    transform: rotate(-2deg); \n  } \n  50% { \n    transform: rotate(1deg); \n  } \n  75% { \n    transform: rotate(-2deg); \n  } \n  100% { \n    transform: rotate(1deg); \n  }  \n}\n\n@keyframes shake {\n  0% { \n    transform: translate(0, 0px) rotate(1deg); \n  } \n  25% { \n    transform: translate(0, 4px) rotate(-2deg); \n  } \n  50% { \n    transform: translate(0, 0px) rotate(1deg); \n  } \n  75% { \n    transform: translate(0, -4px) rotate(-2deg); \n  } \n  100% { \n    transform: translate(0, 0px) rotate(1deg); \n  } \n}\n\n\nmain {\n  position: absolute;\n  top: 0;\n  left: 50%;\n  transform: translate(-50%, 0);\n  width: calc(100% - 64px);\n  max-width: 880px;\n  margin-top: calc(222px + 64px);\n  padding-bottom: 222px;\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: flex-start;\n  gap: 16px;\n}\n\n#memesContainer {\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 16px;\n}\n#memesContainer img {\n  width: 320px;\n  animation: float 3s ease-in-out infinite;\n}\n#memesContainer img:nth-child(odd) {\n  margin-right: auto;\n}\n#memesContainer img:nth-child(even) {\n  margin-left: auto;\n}\n\n#fillerContainer {\n  position: absolute;\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 16px;\n}\n#fillerContainer img {\n  width: 240px;\n}\n#fillerContainer img:nth-child(odd) {\n  ma